/* eslint-disable */
import { collection, getDocs } from "firebase/firestore";
import sanitizeLaTeX from "../utils/sanitize";
import togetherSelect from "./handlers/together/select";
import questionMathinput from "./handlers/question/mathinput"; //    
// ...  

const ENGINE_BY_TYPEKEY = {
  [togetherSelect.typeKey]: togetherSelect,
  [questionMathinput.typeKey]: questionMathinput,
};

/**
 * App.js   :
 * processAndDownloadZip({
 *   templates, selectedTemplateId, extractedBuildData,
 *   setStatusMessage, setIsProcessing, removePagination,
 *   db, appId
 * })
 */
export async function processAndDownloadZip({
  templates,
  selectedTemplateId,
  extractedBuildData,
  setStatusMessage,
  setIsProcessing,
  removePagination,
  db,
  appId,
}) {
  console.log("processAndDownloadZip called");

  

  // mathinput act.js(dap1_array...) 
  const patchMathInputActJs = (actText, subQuestions) => {
    const esc = (s) => String(s ?? "").replace(/\\/g, "\\\\").replace(/"/g, '\\"');

    const answers = (subQuestions || []).map((sq) => sq.answer ?? "");
    const n = Math.max(answers.length, 0);

    //  dap*_array  
    let out = actText.replace(/var\s+dap\d+_array\s*=\s*\[[^\]]*\]\s*;\s*\r?\n/g, "");

    //  (  ) dap1_array..  (multiQuiz     )
    const firstNL = out.indexOf("\n");
    const dapLines = [];
    for (let i = 0; i < n; i++) {
      dapLines.push(`var dap${i + 1}_array = ["${esc(answers[i])}"];`);
    }
    const dapBlock = dapLines.join("\n") + "\n";

    if (firstNL !== -1) out = out.slice(0, firstNL + 1) + dapBlock + out.slice(firstNL + 1);
    else out = dapBlock + out;

    // q_len 
    out = out.replace(/var\s+q_len\s*=\s*\d+\s*;[^\n]*\r?\n/, `var q_len = ${n};// \n`);

    // qArange : [[1],[2],...]
    const qArange = "[" + Array.from({ length: n }, (_, i) => `[${i + 1}]`).join(", ") + "]";
    out = out.replace(
      /var\s+qArange\s*=\s*\[[^\]]*\]\s*;[^\n]*\r?\n/,
      `var qArange = ${qArange};//     \n`
    );

    // dap_array 
    const dapArrayExpr = `[].concat(${Array.from({ length: n }, (_, i) => `dap${i + 1}_array`).join(", ")})`;
    out = out.replace(/var\s+dap_array\s*=\s*[^;]*;[^\n]*\r?\n/, `var dap_array = ${dapArrayExpr};//  \n`);

    return out;
  };

  // --------------------
  // guards
  // --------------------
  if (!window.JSZip) {
    setStatusMessage({
      title: "",
      message: "JSZip   .    .",
      type: "error",
    });
    return;
  }
  if (!selectedTemplateId) {
    setStatusMessage({ title: "", message: "  .", type: "error" });
    return;
  }
  if (!extractedBuildData) {
    setStatusMessage({
      title: "",
      message: "AI   .    .",
      type: "error",
    });
    return;
  }

  setIsProcessing(true);

  try {
    // 1)  
    const templateMeta = templates.find((t) => t.id === selectedTemplateId);
    if (!templateMeta) throw new Error("    .");
    const typeKey = templateMeta.typeKey;
const engine = ENGINE_BY_TYPEKEY[typeKey];

if (!typeKey) throw new Error(" typeKey . (Firestore  )");
if (!engine) throw new Error(` : ${typeKey} (ENGINE_BY_TYPEKEY  )`);

const isTogether = typeKey.startsWith("together.");
const normalizedData = engine.normalize(extractedBuildData);

    // 2) Firestore chunks zip 
    const chunksRef = collection(
      db,
      "artifacts",
      appId,
      "public",
      "data",
      "templates",
      selectedTemplateId,
      "chunks"
    );
    const chunksSnap = await getDocs(chunksRef);
    if (chunksSnap.empty) {
      throw new Error(
        "     . (  )\n     ."
      );
    }

    const sorted = chunksSnap.docs.map((d) => d.data()).sort((a, b) => a.index - b.index);

    const chunkBinaries = sorted.map((c) => {
      const bin = atob(c.data);
      const u8 = new Uint8Array(bin.length);
      for (let i = 0; i < bin.length; i++) u8[i] = bin.charCodeAt(i);
      return u8;
    });

    const totalSize = chunkBinaries.reduce((a, b) => a + b.length, 0);
    const combined = new Uint8Array(totalSize);
    let offset = 0;
    for (const b of chunkBinaries) {
      combined.set(b, offset);
      offset += b.length;
    }

    // 3) zip 
    const zip = new window.JSZip();
    const loadedZip = await zip.loadAsync(combined);

    const parser = new DOMParser();
    const serializer = new XMLSerializer();

    const promises = [];
    const filesToRemove = [];

    loadedZip.forEach((path, file) => {
      if (path.startsWith("common1/")) return;

      // pagination  
      if (removePagination) {
        const fileName = path.split("/").pop();
        if (
          (fileName.startsWith("view") && fileName.endsWith(".html") && !fileName.includes("01")) ||
          (fileName.startsWith("act") && fileName.endsWith(".js") && (fileName.includes("02") || fileName.includes("2")))
        ) {
          filesToRemove.push(path);
          return;
        }
      }

      // --------------------
      // HTML(view*.html)
      // --------------------
      if (path.endsWith(".html")) {
        promises.push(
          file.async("string").then((content) => {
            const parser = new DOMParser();
            const doc = parser.parseFromString(content, "text/html");

            // view01.html -> 0, view02.html -> 1 ( 0)
            const m = path.match(/view(\d+)\.html$/i);
            const pageIndex = m ? Math.max(0, parseInt(m[1], 10) - 1) : 0;

            //    ( HTML   )
            engine.injectHtmlPage({
              doc,
              manifest: templateMeta.manifest || {},
              data: normalizedData,
              pageIndex,
            });

            const serializer = new XMLSerializer();
            loadedZip.file(path, serializer.serializeToString(doc));
          })
        );
      }


      // --------------------
      // JS(act*.js)
      // --------------------
      // 
      if (path.endsWith(".js") && path.includes("act")) {
        promises.push(
          file.async("string").then((content) => {
            // view01/    pageIndex   ,
            // together.select pageIndex  , question.mathinput    
            const pageIndex = 0;

            const patched = engine.patchActJs({
              actJsText: content,
              data: normalizedData,
              pageIndex,
            });

            loadedZip.file(path, patched);
          })
        );
      }
    });

    await Promise.all(promises);
    filesToRemove.forEach((p) => loadedZip.remove(p));

    const blob = await loadedZip.generateAsync({ type: "blob" });
    if (blob.size === 0) throw new Error(" ZIP   0.");

    const a = document.createElement("a");
    a.href = URL.createObjectURL(blob);
    a.download = `Result_${templateMeta.name}_${Date.now()}.zip`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);

    setStatusMessage({ title: "", message: "ZIP    .", type: "success" });
  } catch (e) {
    console.error(e);
    setStatusMessage({ title: "", message: " : " + e.message, type: "error" });
  } finally {
    setIsProcessing(false);
  }
}
