// src/engine/utils/sanitize.js

export const sanitizeLaTeX = (str) => {
  if (!str) return "";
  let sanitized = str;

  // $...$ -> \(...\)
  sanitized = sanitized.replace(/\$(.*?)\$/g, "\\($1\\)");

  // \ ^ _  \( \)  
  if (
    (sanitized.includes("\\") || sanitized.includes("^") || sanitized.includes("_")) &&
    !sanitized.includes("\\(")
  ) {
    sanitized = `\\(${sanitized}\\)`;
  }

  //  \( \( 
  sanitized = sanitized.replace(/\\\((\\\(.*?\\\))\\\)/g, "$1");

  return sanitized;
};
